





































import { Component, Vue } from "vue-property-decorator";
import assets from "@/assets/assets.json";
import { CreateElement, VNode } from "vue";

const Balance = Vue.extend({
  name: "Balance",
  props: { balance: { type: String, default: "12.212" } },
  render(h: CreateElement): VNode {
    return h(
      "div",
      {
        staticClass: "balance-component",
        on: {
          click: () => {
            this.$emit("click:balance", this.balance);
          },
        },
      },
      [this.balance],
    );
  },
});

Vue.component("balance", Balance);

@Component
class FormTips extends Vue {
  value = "";

  asset = null;

  selectable = true;

  precision = 2;

  get assets() {
    return assets.map((asset) => {
      const chainAsset = assets.find((a) => a.asset_id === asset.chain_id);
      return {
        id: asset.asset_id,
        symbol: asset.symbol,
        name: asset.name,
        logo: asset.icon_url,
        label: asset.label,
        chainLogo: chainAsset && chainAsset.icon_url,
      };
    });
  }

  get tips() {
    return [
      {
        text: "这是一条错误消息",
        error: true,
      },
      {
        component: "balance",
      },
      {
        text: "手续费：1 BTC",
      },
      {
        text: "手续费：1.23 BTC",
        hide: true,
      },
      {
        text: "最小值：100 BTC",
      },
      {
        text:
          "这是一条很长很长很长很长很长很长很长很长很长很长很长很长很长很长很长很长很长很长很长很长很长",
      },
    ];
  }

  handleClickBalance(balance) {
    this.value = balance;
  }
}
export default FormTips;
